.logo {
    min-height: 30px;
    width: 150px;
    max-height: 100px;
    min-width: 20px;
    height: 150px;
    max-width: 100px;
    border-radius: 50px;
}

.logoContainer {
    width: 130px;
}

.subscribe {
    border: none;
    outline: none;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    border-radius: 20px;
    width: 100px;
    height: 40px;
    font-size: 15px;
    font-family: Roboto, Verdana, Tahoma, sans;
    padding: 5px 10px;
    background-color: white;
}
.subscribe:hover {
    transition: all .3s linear;
    -webkit-transition: all .3s linear;
    -moz-transition: all .3s linear;
    background: aliceblue;

}

.description {
    display: inline-block;
    vertical-align: top;
    margin-left: 130px;
    margin-top: -130px;
}

.descriptionContainer {
    margin-bottom: 10px;
    margin-left: 10px;
}

.name {
    margin-top: -30px;
    font-weight: 700;
    font-size: 23px;
    display: block;
    vertical-align: top;
}

.status {
    margin-top: 9px;
}